@import '../spritesmith-generated/sprite.css';

.container {
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;

    display: flex;
}

.animation-container {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
}

.frameset {
    position: absolute;
    display: flex;
}

.frameset.bottom-obj {
    bottom: 0;
}

.frameset.bottom-obj.animation-frame {
    bottom: 110px;
}

.frameset.top-obj.animation-frame {
    top: 10px;
}

.frameset.bottom-obj.animation-frame > .obj-1 {
    position: absolute;
}

.frameset.bottom-obj.animation-frame .invisible,
.frameset.top-obj.animation-frame .invisible {
    display: none;
}

/* RENDERING_PERFORMANCE_PAINT_RECT_GPU: true */
.animation-container,
.frameset.bottom-obj.back-sky,
.frameset.bottom-obj.forest,
.frameset.bottom-obj.ground {
    will-change: transform;
}

.img-background {
    background-image: url('../image/background.png');
    width: 1096px;
    height: 822px;
}
